<template>
  <div class="chat-container">
    <div class="chat-title" v-if="customer">
      <img v-if="customer.avatar" :src="customer.avatar" class="chat-avatar"/>
      <img v-else src="../images/default.png" class="chat-avatar"/>
      <div class="chat-name">{{ renderCustomerName(customer) }}</div>
    </div>
    <div ref="scrollView" class="chat-main">
      <div ref="messageList" class="message-list">
        <div v-if="history.loading" class="history-loading">
          <img src="../images/pending.gif"/>
        </div>
        <div v-else class="history-loaded" @click="loadHistoryMessage(false)">
          {{ history.allLoaded ? '已经没有更多的历史消息' : '获取历史消息' }}
        </div>
        <div v-for="(message, index) in history.messages" :key="index">
          <div class="time-tips">
            {{ renderMessageDate(message, index) }}
          </div>
          <div class="message-item">
            <div v-if="message.type === 'CS_ACCEPT'" class="accept-message">
              {{ message.senderData.name }}已接入
            </div>
            <div v-else-if="message.type === 'CS_END'" class="accept-message">
              {{ message.senderData.name }}已结束会话
            </div>
            <div v-else-if="message.type === 'CS_TRANSFER'" class="accept-message">
              {{
                message.senderId === currentAgent.id ? `已转接给` + message.payload.transferTo.data.name :
                  '已接入来自' + message.senderData.name + '的转接'
              }}
            </div>
            <div v-else :class="{ self: message.senderId !== customer.id }" class="message-item-content">
              <div class="sender-info">
                <img v-if="message.senderData.avatar" :src="message.senderData.avatar" class="sender-avatar"/>
                <img v-else src="../images/default.png" class="sender-avatar"/>
                <img v-if="message.senderData.platform === 'wxapp'" src="../images/wxapp.png" class="wxapp-icon" />
                <div class="sender-name" v-if="message.senderId !== customer.id">{{ message.senderData.name }}</div>
              </div>
              <div class="message-content">
                <div class="message-payload">
                  <div v-if="message.status === 'sending'" class="pending"></div>
                  <div v-if="message.status === 'fail'" class="send-fail"></div>
                  <div v-if="message.type === 'text'" class="content-text"
                       v-html="message.payload.text"></div>
                  <div v-if="message.type === 'image'" class="content-image"
                       @click="showImage(message.payload.url)">
                    <img :src="message.payload.url"
                         :style="{height:getImageHeight(message.payload.width,message.payload.height)+'px'}"/>
                  </div>
                  <goeasy-audio-player
                    v-if="message.type ==='audio'"
                    :duration="message.payload.duration"
                    :src="message.payload.url"
                  />
                  <div v-if="message.type === 'video'" class="content-video" @click="showVideo(message.payload.video.url)">
                    <img :style="{height:getImageHeight(message.payload.thumbnail.width,message.payload.thumbnail.height)+'px'}" :src="message.payload.thumbnail.url"/>
                    <div class="icon"></div>
                  </div>
                  <a v-if="message.type === 'file'" :href="message.payload.url" target="_blank" download="download" title="点击下载文件">
                    <div class="content-file">
                      <div class="file-info">
                        <span class="file-name">{{ message.payload.name }}</span>
                        <span class="file-size">{{ (message.payload.size / 1024).toFixed(2) }}KB</span>
                      </div>
                      <img class="file-img" src="../images/file.png" />
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <span ref="bottomView"></span>
    </div>
    <div class="chat-footer">
      <div v-if="customerStatus==null" class="accept-session">
        <div>离线中</div>
      </div>
      <div v-else-if="customerStatus.status==='PENDING'" class="accept-session">
        <div class="accept-info">
          会话已等待{{ pendingTime.duration }}
        </div>
        <button class="accept-btn" @click="acceptSession">立即接入</button>
      </div>
      <div v-else-if="customerStatus.status==='ACCEPTED' && currentAgent.id !== customerStatus.agent.id"
           class="accept-session">
        <div class="accept-info">{{ customerStatus.agent.data.name }}已接入</div>
      </div>
      <div v-else-if="customerStatus.status==='FREE'" class="accept-session">
        <button class="accept-btn" @click="acceptSession">主动接入</button>
      </div>
      <div v-else class="action-box">
        <div class="action-bar">
          <div class="chat-action">
            <!-- 表情 -->
            <div class="action-item">
              <emoji-picker @emoji="chooseEmoji">
                <i slot="emoji-invoker" slot-scope="events"
                   @click.stop="events.events.click" class="iconfont icon-smile" title="表情"></i>
                <div slot="emoji-picker" slot-scope="data">
                  <div class="emoji-box">
                    <div v-for="(emojiGroup, category) in data.emojis" :key="category">
                      <div v-if="category !=='Frequently used'" class="emoji-item">
                              <span v-for="(emoji, emojiName) in emojiGroup" :key="emojiName"
                                    @click="data.insert(emoji)" :title="emojiName">{{ emoji }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </emoji-picker>
            </div>
            <!-- 图片 -->
            <div class="action-item">
              <label for="img-input">
                <i class="iconfont icon-picture" title="图片"></i>
              </label>
              <input v-show="false" id="img-input" accept="image/*" multiple type="file"
                     @change="sendImageMessage"/>
            </div>
            <!-- 视频 -->
            <div class="action-item">
              <label for="video-input"><i class="iconfont icon-film" title="视频"></i></label>
              <input v-show="false" id="video-input" accept="video/*" type="file"
                     @change="sendVideoMessage"/>
            </div>
            <!-- 文件 -->
            <div class="action-item">
              <label for="file-input">
                <i class="iconfont icon-wj-wjj" title="文件"></i>
              </label>
              <input
                type="file"
                @change="sendFileMessage"
                id="file-input"
                v-show="false"
              />
            </div>
          </div>
          <div class="session-action">
            <i class="iconfont icon-zhuanban" title="转接" @click="showTransferForm"></i>
            <i class="iconfont icon-end_chat" title="结束会话" @click="endSession"></i>
          </div>
        </div>
        <div class="input-box">
          <textarea
            @paste="pasteImageMessage" @keydown="sendMessageByEnter" ref="input" v-model="text"
            autocomplete="off" class="input-content"
            placeholder="请输入消息，按Enter键发送，Shift+Enter键进行换行"
          ></textarea>
        </div>
        <div class="send-box">
          <button class="send-button" @click="sendTextMessage">发送</button>
        </div>
      </div>
    </div>
    <!-- 转接弹窗 -->
    <div v-if="transferForm.visible" class="transfer-popup">
      <div class="transfer-model">
        <div v-if="transferForm.agents.length" class="transfer-content">
          <div v-for="(agent, index) in transferForm.agents" class="agent-info">
            <label class="agent-label">
              <input v-model="transferForm.to" :name="agent.data.name" :value="agent" type="radio"/>
              <img :src="agent.data.avatar" class="agent-avatar"/>
              <span class="agent-name">{{ agent.data.name }}</span>
            </label>
          </div>
        </div>
        <div v-else class="transfer-content">
          <div class="no-agent">-当前无其他客服在线-</div>
        </div>
        <div class="transfer-bottom">
          <span v-if="transferForm.agents.length" class="transfer-button" @click="transfer()">确认</span>
          <span class="transfer-button" @click="hideTransferForm()">取消</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import EmojiPicker from 'vue-emoji-picker'
  import {formatDate, formateTime, formateFullDate} from '../lib/utils.js'
  import GoEasyAudioPlayer from "../components/GoEasyAudioPlayer";

  const IMAGE_MAX_WIDTH = 200;
  const IMAGE_MAX_HEIGHT = 150;
  export default {
    name: "Chat",
    components: {
      "goeasy-audio-player": GoEasyAudioPlayer,
      "emoji-picker": EmojiPicker,
    },
    data() {
      return {
        currentAgent: null,
        csteam: null,

        customer: null,
        customerStatus: null,

        to: {},//用于创建消息时传入

        history: {
          messages: [],
          allLoaded: false,
          loading: true
        },

        text: '',
        transferForm: {
          visible: false,
          agents: [],
          to: {}
        },
        pendingTime: {
          timer: null,
          duration: ''
        }
      }
    },
    async created() {
      const openId = this.$route.query.id;
      let data = await this.global.api.getContact(openId);
      this.customer = {
        id: data.data.openId,
        name: data.data.nickname,
        avatar: data.data.avatar,
        unionId: data.data.unionid, //todo?有用吗？
        appName: data.data.appName,
        mark: data.data.mark,
        mobileNumber: data.data.mobileNumber
      }

      this.to = {
        type: this.global.GoEasy.IM_SCENE.CS,
        id: this.customer.id,
        data: this.customer,
      };
      this.currentAgent = this.global.currentAgent;
      this.csteam = this.global.goEasy.im.csteam(this.currentAgent.teamId);

      this.liveSession();
      this.loadHistoryMessage(true);
    },
    beforeDestroy() {
      this.csteam.quitLiveSession({
        onSuccess: () => {
          console.log('quit successfully ');
        },
        onFailed: (error) => {
          console.log('failed to quit:', error);
        },
      });
      if (this.pendingTime.timer) {
        clearInterval(this.pendingTime.timer);
      }
    },
    methods: {
      renderCustomerName (customer) {
        if (customer.mark) {
          return customer.mark;
        } else if (customer.name.indexOf('游客') !== -1 && customer.mobileNumber || !customer.name) {
          return customer.mobileNumber;
        }
        return customer.mobileNumber;
      },
      liveSession() {
        this.csteam.liveSession({
          customerId: this.customer.id,
          onSuccess: () => {
            console.log('live successfully');
            this.markMessageAsRead();
          },
          onFailed: (error) => {
            console.log('failed to live session:', error);
          },
          onStatusUpdated: (customerStatus) => {
            this.customerStatus = customerStatus;
            if (customerStatus.status==='PENDING') {
              this.updatePendingTime(customerStatus.start);
            }
          },
          onNewMessage: (message) => {
            this.onReceivedMessage(message);
          },
        })
      },
      updatePendingTime (time) {
        clearInterval(this.pendingTime.timer);
        this.pendingTime.timer = setInterval(() => {
          this.pendingTime.duration = formateTime(time);
        },1000);
      },
      onReceivedMessage(newMessage) {
        //如果该消息已存在，跳过
        if (this.history.messages.findIndex((message) => newMessage.id === message.messageId) >= 0) {
          return;
        }
        this.history.messages.push(newMessage);
        this.markMessageAsRead();
        this.scrollToBottom();
      },
      markMessageAsRead() {
        this.csteam.markMessageAsRead({
          type: this.global.GoEasy.IM_SCENE.CS,
          id: this.customer.id,
          onSuccess: function () {
            console.log('标记已读成功');
          },
          onFailed: function (error) {
            console.log('标记已读失败', error);
          }
        });
      },
      loadHistoryMessage(scrollToBottom) {
        this.history.loading = true;
        let lastMessageTimeStamp;
        let lastMessage = this.history.messages[0];
        if (lastMessage) {
          lastMessageTimeStamp = lastMessage.timestamp;
        }
        let limit = 10;
        this.csteam.history({
          id: this.customer.id,
          type: this.global.GoEasy.IM_SCENE.CS,
          lastTimestamp: lastMessageTimeStamp,
          limit: limit,
          onSuccess: (result) => {
            this.history.loading = false;
            let messages = result.content;
            if (messages.length === 0) {
              this.history.allLoaded = true;
            } else {
              if (lastMessageTimeStamp) {
                this.history.messages = messages.concat(this.history.messages);
              } else {
                this.history.messages = messages;
              }
              if (scrollToBottom) {
                this.scrollToBottom();
              }
            }
          },
          onFailed: (error) => {
            //获取失败
            this.history.loading = false;
            console.log('获取历史消息失败, code:' + error.code + ',错误信息:' + error.content);
          },
        });
      },
      /**
       * 核心就是设置高度，产生明确占位
       *
       * 小  (宽度和高度都小于预设尺寸)
       *    设高=原始高度
       * 宽 (宽度>高度)
       *    高度= 根据宽度等比缩放
       * 窄  (宽度<高度)或方(宽度=高度)
       *    设高=MAX height
       *
       * @param width,height
       * @returns number
       */
      getImageHeight(width, height) {
        if (width < IMAGE_MAX_WIDTH && height < IMAGE_MAX_HEIGHT) {
          return height;
        } else if (width > height) {
          return IMAGE_MAX_WIDTH / width * height;
        } else if (width === height || width < height) {
          return IMAGE_MAX_HEIGHT;
        }
      },
      renderMessageDate(message, index) {
        if (index === 0) {
          return formatDate(message.timestamp);
        } else {
          if (message.timestamp - this.history.messages[index - 1].timestamp > 5 * 60 * 1000) {
            return formatDate(message.timestamp);
          }
        }
        return '';
      },
      showImage(url) {
        this.callParentFunction('showImage', {url:url});
      },
      showVideo (url) {
        this.callParentFunction('showVideo', {url:url});
      },
      callParentFunction (functionName, parameters) {
        const targetOrigin = document.referrer;
        window.parent.postMessage({
          'function': functionName,
          'parameters': parameters,
        }, targetOrigin);
      },

      isOnline() {
        return new Promise((resolve, reject) => {
          this.csteam.isOnline({
            onSuccess: (result) => {
              resolve(result);
            },
            onFailed: (error) => {
              console.log('获取在线状态失败，error:', error)
              reject(error);
            }
          })
        })
      },
      async acceptSession() {
        if (await this.isOnline()) {
          this.csteam.accept({
            id: this.customer.id,
            onSuccess: () => {
              this.sendWelcome();
              console.log('accept successfully.');
              clearInterval(this.pendingTime.timer);
            },
            onFailed: (error) => {
              console.log('accept failed', error);
            }
          });
        } else {
          alert('您还不是一名该团队的在线客服，请点击左下角头像进行上线操作')
        }
      },

      sendWelcome() {
        this.csteam.createTextMessage({
          text: this.global.welcome,
          to: this.to,
          onSuccess: (message) => {
            this.sendMessage(message);
          },
          onFailed: (err) => {
            console.log("创建消息err:", err);
          }
        });
      },
      endSession() {
        this.csteam.end({
          id: this.customer.id,
          onSuccess: () => {
            console.log('end successfully.');
          },
          onFailed: (error) => {
            console.log('end failed', error);
          }
        })
      },
      showTransferForm() {
        this.csteam.agents({
          onSuccess: (result) => {
            this.transferForm.visible = true;
            this.transferForm.agents = result.content.filter((agent) => {
              return agent.id !== this.currentAgent.id;
            });
          },
          onFailed: (error) => {
            console.log('query online agents failed', error);
          }
        });
      },
      transfer() {
        this.csteam.transfer({
          customerId: this.customer.id,
          agentId: this.transferForm.to.id,
          onSuccess: () => {
            this.transferForm.visible = false;
            console.log('transfer successfully.');
          },
          onFailed: (error) => {
            console.log('transfer failed', error);
          }
        })
      },
      hideTransferForm() {
        this.transferForm.visible = false;
      },
      pasteImageMessage(e) {
        const file = e.clipboardData.files[0]
        if (!file) {
          return
        }
        if (file.type === 'image/png') {
          this.csteam.createImageMessage({
            file: file,
            to: this.to,
            wxmpTemplateMsg: this.getWxmpTemplateMsg(),
            onProgress: function (progress) {
              console.log(progress)
            },
            onSuccess: (message) => {
              this.sendMessage(message);
            },
            onFailed: (e) => {
              console.log('error :', e);
            }
          });
        }
      },
      sendMessageByEnter(event) {
        if (event.shiftKey && event.keyCode === 13) { // shift+enter换行
          event.preventDefault();
          this.text = this.text + "\n";
        } else if (event.keyCode === 13) { // enter发送
          event.preventDefault();
          this.sendTextMessage();
        }
      },
      chooseEmoji(emojiKey) {
        this.text += emojiKey;
      },
      getWxmpTemplateMsg () {
        let wxmpTemplateMsg = {
          template_id:'EG1cbpvmMJfb_62mgqRT8JuLUTWElfDF97zcpCt2w8U',
          data:{
            first: {
              value: `亲爱的${this.customer.name}，您的咨询，有新的回复~`,
              color: "#D02129"
            },
            keyword1: {
              value: formateFullDate(Date.now()),
              color: ""
            },
            keyword2: {
              value: "点击我，进入小程序查看回复详情~",
              color: ""
            },
            remark: {
              value: "您也可以通过电脑访问GoEasy官网与我们更高效的沟通~",
              color: "#D02129"
            },
          },
          url:'',
          miniprogram:{
            appid:'wxc4519c8ba7fd9a5b',
            pagepath:'pages/index/index'
          }
        }
        return wxmpTemplateMsg
      },
      sendTextMessage() {
        if (this.text.trim().length === 0) {
          console.log('输入为空');
          return
        }
        this.csteam.createTextMessage({
          text: this.text,
          to: this.to,
          wxmpTemplateMsg: this.getWxmpTemplateMsg(),
          onSuccess: (message) => {
            this.sendMessage(message);
            this.text = '';
          },
          onFailed: (err) => {
            console.log("创建消息err:", err);
          }
        });
      },
      sendImageMessage(e) {
        let fileList = [...e.target.files];
        fileList.forEach((file) => {
          this.csteam.createImageMessage({
            file: file,
            to: this.to,
            wxmpTemplateMsg: this.getWxmpTemplateMsg(),
            onProgress: function (progress) {
              console.log(progress)
            },
            onSuccess: (message) => {
              this.sendMessage(message);
            },
            onFailed: (e) => {
              console.log('error :', e);
            }
          });
        })
      },
      sendVideoMessage(e) {
        const file = e.target.files[0];
        this.csteam.createVideoMessage({
          file: file,
          to: this.to,
          wxmpTemplateMsg: this.getWxmpTemplateMsg(),
          onProgress: function (progress) {
            console.log(progress)
          },
          onSuccess: (message) => {
            this.sendMessage(message);
          },
          onFailed: (e) => {
            console.log('error :', e);
          }
        });
      },
      sendFileMessage(e) {
        const file = e.target.files[0];
        this.csteam.createFileMessage({
          file: file,
          to: this.to,
          wxmpTemplateMsg: this.getWxmpTemplateMsg(),
          onProgress: function (progress) {
            console.log(progress)
          },
          onSuccess: (message) => {
            this.sendMessage(message);
          },
          onFailed: (e) => {
            console.log('error :', e);
          }
        });
      },
      sendMessage(message) {
        this.history.messages.push(message);
        this.scrollToBottom();
        this.global.goEasy.im.sendMessage({
          message: message,
          onSuccess: (message) => {
            console.log('发送成功', message);
          },
          onFailed: function (error) {
            if (error.code === 507) {
              console.log('发送语音/图片/视频/文件失败，没有配置OSS存储，详情参考：https://www.goeasy.io/cn/docs/goeasy-2.x/im/message/media/send-media-message.html');
            } else {
              console.log('发送失败:', error);
            }
          }
        });
      },
      scrollToBottom() {
        this.$nextTick(() => {
          if (this.$refs.bottomView) {
            this.$refs.bottomView.scrollIntoView();
          }
        })
      }
    }
  };
</script>

<style lang="scss" scoped>
