    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .chat-title {
      height: 61px;
      padding: 15px;
      display: flex;
      align-items: center;
      font-size: 18px;

      .chat-avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }

      .chat-name {
        width: 400px;
        margin-left: 10px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
      }

    }

    .chat-main {
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      flex: 1;

      // 滚动条
      &::-webkit-scrollbar {
        width: 10px;
        height: 1px;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: #E5E5E5;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
      }
      scrollbar-width: thin;

      .message-list {
        padding: 0 15px;
      }

      .history-loaded {
        text-align: center;
        font-size: 12px;
        color: #d02129;
        cursor: pointer;
        line-height: 20px;
      }

      .history-loading {
        width: 100%;
        text-align: center;
      }

      .time-tips {
        color: #999;
        text-align: center;
        font-size: 12px;
      }

      .message-item {
        display: flex;

        .accept-message {
          width: 100%;
          text-align: center;
          color: #606164;
          line-height: 25px;
          font-size: 12px;
        }

        .message-item-content {
          flex: 1;
          max-height: 230px;
          margin: 5px 0;
          overflow: hidden;
          display: flex;

          .sender-info {
            margin: 5px;
            position: relative;

            .sender-avatar {
              width: 35px;
              height: 35px;
              border-radius: 50%;
            }

            .wxapp-icon {
              position: absolute;
              right: 0;
              top: 25px;
              width: 15px;
              height: 15px;
              background: #f7f7f7;
              border-radius: 50%;
            }

            .sender-name {
              color: #606164;
              text-align: center;
              font-size: 12px;
            }

          }

          .message-content {
            max-width: calc(100% - 100px);

            .message-payload {
              display: flex;
              align-items: center;
              a {
                text-decoration: none;
              }
            }

            .pending {
              background: url("../images/pending.gif") no-repeat center;
              background-size: 13px;
              width: 15px;
              height: 15px;
            }

            .send-fail {
              background: url("../images/failed.png") no-repeat center;
              background-size: 13px;
              width: 15px;
              height: 15px;
            }

            .content-text {
